import * as React from "react";
import TopNav from "./topNav/TopNav";
import { Box } from "theme-ui";
import SideNav from "./sideNav/SideNav";
import ComponentMenu from "./menus/componentMenu";
import BrandMenu from "./menus/brandMenu";
import FoundationMenu from "./menus/foundationMenu";
import ContentMenu from "./menus/contentMenu";
import ResourcesMenu from "./menus/resourcesMenu";

interface LayoutProps {
  location: Location;
  children: React.ReactNode;
}

const Layout = ({ children, location }: LayoutProps) => {
  return (
    <>
      <TopNav location={location} />
      <SideNav>
        {location.pathname.includes("/brand") &&
          <BrandMenu location={location}></BrandMenu>
        }
        {location.pathname.includes("/components") &&
          <ComponentMenu ></ComponentMenu>
        }
        {location.pathname.includes("/foundations") &&
          <FoundationMenu></FoundationMenu>
        }
        {location.pathname.includes("/content") &&
          <ContentMenu></ContentMenu>
        }
        {location.pathname.includes("/resources") &&
          <ResourcesMenu></ResourcesMenu>
        }
      </SideNav>
      <Box>
        {children}
      </Box>
    </>
  );
};

export default Layout;
