import React from 'react'
import { MenuList, MenuItem, Text } from 'fusion'
import { navigate } from 'gatsby';

enum page {
    one = '/content/writing-guidelines',
    two = '/content/cre-vocabulary',
    three = '/content/ui-vocabulary',
    four = '/content/voice-and-tone',
    five = '/content/formatting',
  }

export default function contentMenu() {
  return (
    <MenuList>
      <Text variant="small" weight="semibold" color="textSecondary" sx={{ pt: 2, pb: 1, px: 3 }}>
        Content
      </Text>
      <MenuItem selected={location.pathname.includes(page.one)} onClick={() => navigate(page.one)}> Writing guidelines </MenuItem>
      <MenuItem selected={location.pathname.includes(page.five)} onClick={() => navigate(page.five)}> Formatting for localization </MenuItem>
      <MenuItem disabled selected={location.pathname.includes(page.two)} onClick={() => navigate(page.two)}> CRE vocabulary </MenuItem>
      <MenuItem disabled selected={location.pathname.includes(page.three)} onClick={() => navigate(page.three)}> UI vocabulary </MenuItem>
      <MenuItem disabled selected={location.pathname.includes(page.four)} onClick={() => navigate(page.four)}> Voice and tone </MenuItem>
    </MenuList>
  )
}
