import React from 'react'
import { MenuList, MenuItem, Text } from 'fusion'
import { navigate } from 'gatsby'

enum page {
    one = '/resources/component-status',
    two = '/resources/icon-library',
    three = '/resources/illustration-library',
  }

export default function resourcesMenu() {
  return (
    <MenuList>
      <Text variant="small" weight="semibold" color="textSecondary" sx={{ pt: 2, pb: 1, px: 3 }}>
        Resources
      </Text>
      <MenuItem disabled selected={location.pathname.includes(page.one)} onClick={() => navigate(page.one)}> Component status </MenuItem>
      <MenuItem disabled selected={location.pathname.includes(page.two)} onClick={() => navigate(page.two)}> Icon library </MenuItem>
      <MenuItem disabled selected={location.pathname.includes(page.three)} onClick={() => navigate(page.three)}> Illustration library </MenuItem>
    </MenuList>
  )
}
