import React from 'react'
import { MenuList, MenuItem, Text } from 'fusion'
import { navigate } from 'gatsby';

enum page {
  one = '/brand/values-and-goals',
  two = '/brand/what-is-fusion',
}

export default function BrandMenu({ location }: { location: Location }) {

  return (
    <MenuList>
      <Text variant="small" weight="semibold" color="textSecondary" sx={{ pt: 2, pb: 1, px: 3 }}>
        Brand
      </Text>
      <MenuItem selected={location.pathname.includes(page.two)} onClick={() => navigate(page.two)}> What is Fusion? </MenuItem>
      <MenuItem selected={location.pathname.includes(page.one)} onClick={() => navigate(page.one)}> Values and goals </MenuItem>
    </MenuList>
  )
}
