import { theme, Flex, IconButton, NavBar, Box, Tab, Tabs, ColorMode, IconAltusGroupWordmark, IconFusionLogo, TextField, IconBell, IconSearch, IconSettings, IconMenu, Drawer, IconX, Tooltip, Link } from 'fusion'
import React from 'react'
import { navigate } from 'gatsby';
import useMediaQuery from '@mui/material/useMediaQuery';
import ComponentMenu from '../menus/componentMenu';
import BrandMenu from '../menus/brandMenu';
import FoundationMenu from '../menus/foundationMenu';
import "./details.css"
import FusionLogoH from '../../images/fusion-logo-h.svg';
import ContentMenu from '../menus/contentMenu';
import ResourcesMenu from '../menus/resourcesMenu';

const mode = ColorMode.dark;

enum page {
  home = '/',
  brand = '/brand',
  components = '/components',
  foundations = '/foundations',
  patterns = '/patterns',
  content = '/content',
  resources = '/resources'
}

const TopNav = ({ location }: { location: Location }) => {
  const tab: string = `/${location?.pathname.split('/')[1] || ''}`;
  const isMobile = useMediaQuery("(max-width:780px)");
  const [isOpen, setIsOpen] = React.useState(false);
  return (
    <>
      {isMobile &&
        <Drawer id="drawer" variant="temporary" open={isOpen} onClose={() => setIsOpen(false)}>
          <Box style={{ width: "320px", padding: "12px" }}>
            <IconButton variant="tertiary" style={{ float: "right" }} icon={IconX} onClick={() => setIsOpen(false)} />
            <Box className="detailContainer">
              <details open={location.pathname.includes("brand")}>
                <summary>Brand</summary>
                <Box onClick={() => setIsOpen(false)}>
                  <BrandMenu location={location}></BrandMenu>
                </Box>
              </details>
              <details open={location.pathname.includes("foundations")}>
                <summary>Foundations</summary>
                <Box onClick={() => setIsOpen(false)}>
                  <FoundationMenu></FoundationMenu>
                </Box>
              </details>
              <details open={location.pathname.includes("components")}>
                <summary>Components</summary>
                <Box onClick={() => setIsOpen(false)}>
                  <ComponentMenu></ComponentMenu>
                </Box>
              </details>
              <details open={location.pathname.includes("content")}>
                <summary>Content</summary>
                <Box onClick={() => setIsOpen(false)}>
                  <ContentMenu></ContentMenu>
                </Box>
              </details>
              <details open={location.pathname.includes("resources")}>
                <summary>Resources</summary>
                <Box onClick={() => setIsOpen(false)}>
                  <ResourcesMenu></ResourcesMenu>
                </Box>
              </details>
            </Box>
          </Box>
        </Drawer>
      }
      <NavBar sx={{ justifyContent: 'space-between', boxShadow: theme.shadows.depth2, zIndex: "2", minWidth: "320px" }}>
        <Flex>
          {isMobile && <IconButton mr={3} icon={IconMenu} onClick={() => setIsOpen(true)} />}
          {/* <Tooltip style={{ fontSize: "12px" }} arrow={true} title="Home" components={undefined} componentsProps={undefined}> */}
            <Link mr={1} style={{ cursor: "pointer", height: "32px!important" }} href="/" >
              {/* <IconFusionLogo size="large" /> */}
              <img src={FusionLogoH} style={{ width: "88px" }} />
              {/* <IconAltusGroupWordmark style={{ height: "auto", width: "64px" }} /> */}
            </Link>
          {/* </Tooltip> */}
          {!isMobile &&
            <Tabs value={tab || ""} mode={ColorMode.dark} >
                <Tab label="Brand" value={page.brand} onClick={() => navigate(page.brand)} />
                <Tab label="Foundations" value={page.foundations} onClick={() => navigate(page.foundations)} />
                <Tab disabled label="Components" value={page.components} onClick={() => navigate(page.components)} />
                <Tab label="Content" value={page.content} onClick={() => navigate(page.content)} />
                {/* <Tab disabled label="Patterns" value={page.patterns} onClick={() => navigate(page.patterns)} /> */}
                <Tab disabled label="Resources" value={page.resources} onClick={() => navigate(page.resources)} />
            </Tabs>
          }
        </Flex>
        <Flex>
          {/* <TextField dense style={{ width: 200 }} iconStart={IconSearch} placeholder="Search" /> */}
          {/* <Tooltip style={{ fontSize: "12px" }} arrow={true} title="Search" components={undefined} componentsProps={undefined}> */}
            <IconButton disabled variant="tertiary" mode={mode} sx={{ mx: 1 }} icon={IconSearch} />
          {/* </Tooltip> */}
          {/* <Tooltip style={{ fontSize: "12px" }} arrow={true} title="Notifications">
            <IconButton disabled variant="tertiary" mode={mode} sx={{ mx: 1 }} icon={IconBell} />
          </Tooltip>
          <Tooltip style={{ fontSize: "12px" }} arrow={true} title="Settings">
            <IconButton disabled variant="tertiary" mode={mode} sx={{ mx: 1 }} icon={IconSettings} />
          </Tooltip> */}
        </Flex>
      </NavBar>
    </>
  )
}

export default TopNav;
