import React from 'react'
import { MenuList, MenuItem, Text } from 'fusion'
import { navigate } from 'gatsby'

enum page {
  one = '/foundations/accessibility',
  two = '/foundations/color',
  three = '/foundations/elevation',
  four = '/foundations/icons',
  five = '/foundations/illustrations',
  six = '/foundations/logos',
  seven = '/foundations/space',
  eight = '/foundations/typography',
  nine = '/foundations/dataviz',
}

export default function foundationMenu() {
  return (
    <MenuList>
    <Text variant="small" weight="semibold" color="textSecondary" sx={{ pt: 2, pb: 1, px: 3 }}>
      Foundations
    </Text>
    <MenuItem selected={location.pathname.includes(page.one)} onClick={() => navigate(page.one)} >Accessibility</MenuItem>
    <MenuItem selected={location.pathname.includes(page.two)} onClick={() => navigate(page.two)} >Color</MenuItem>
    <MenuItem selected={location.pathname.includes(page.nine)} onClick={() => navigate(page.nine)} >Data visualization</MenuItem>
    <MenuItem selected={location.pathname.includes(page.three)} onClick={() => navigate(page.three)} >Elevation</MenuItem>
    <MenuItem selected={location.pathname.includes(page.four)} onClick={() => navigate(page.four)} >Icons</MenuItem>
    <MenuItem selected={location.pathname.includes(page.five)} onClick={() => navigate(page.five)} >Illustrations</MenuItem>
    <MenuItem selected={location.pathname.includes(page.six)} onClick={() => navigate(page.six)} >Logos</MenuItem>
    <MenuItem selected={location.pathname.includes(page.seven)} onClick={() => navigate(page.seven)} >Space</MenuItem>
    <MenuItem selected={location.pathname.includes(page.eight)} onClick={() => navigate(page.eight)} >Typography</MenuItem>
  </MenuList> 
  )
}
